.form_row {
  display: flex;
  align-items: center;
  margin-top: 20px;
  margin-bottom: 20px;
}
.form_radio label {
  margin-right: 15px;
}
.form_select select {
  border: none;
  border-bottom: 1px solid purple;
  padding: 5px 0px;
}
.form_txtarea {
  margin-bottom: 20px;
}
.form_txtarea textarea {
  width: 100%;
  height: 100px;
  border-radius: 5px;
  /*border: 1px solid purple;*/
}
.form_btn button:first-child {
  background-color: purple;
  color: #fff;
  margin-bottom: 20px;
}
.form_btn button {
  width: 100%;
  height: 50px;
  border: 1px solid purple;
  border-radius: 5px;
  font-size: 14px;
  color: purple;
  background-color: transparent;
}
.makeStyles-mainContainer-23 {
  overflow: auto;
}

.MuiCheckbox-colorSecondary.Mui-checked {
  color: #7b72af;
}

.MuiButton-root {
  text-transform: capitalize;
}
